<template>
    <div class="skillConfig">
        <div class="title">技能组配置</div>
        <div class="nav">
            <el-button size="mini" type="primary" @click="addSkillListOpen">新增</el-button>
            <span>技能组名称：</span>
            <el-input
                class="inputC"
                placeholder="请输入技能组名称"
                v-model="selectValue"
                size="mini"
                clearable>
            </el-input>
            <span>创建人：</span>
            <el-input
                class="inputC"
                placeholder="请输入创建人"
                v-model="creatorName"
                size="mini"
                clearable>
            </el-input>
            <span>创建时间：</span>
            <el-date-picker
                v-model="ctime"
                class="inputC"
                size="mini"
                type="date"
                placeholder="选择日期"
                value-format="yyyy-MM-dd">
            </el-date-picker>
            <span>修改时间：</span>
            <el-date-picker
                v-model="utime"
                class="inputC"
                size="mini"
                type="date"
                placeholder="选择日期"
                value-format="yyyy-MM-dd">
            </el-date-picker>
            <el-button size="mini" type="primary" @click="getSkillList">搜索</el-button>
            <el-button size="mini" type="primary" @click="importJnCongigExcel">导出技能组配置</el-button>
        </div>
        <div class="table-con">
            <el-table
                v-loading="loading"
                border
                :data="tableData"
                style="width: 100%"
                :row-class-name="tableRowClassName">
                <el-table-column
                    label="序号"
                    align="center"
                    type="index"
                    width="70">
                </el-table-column>
                <el-table-column
                    prop="skillGroup"
                    label="技能组名称">
                </el-table-column>
                <el-table-column
                    prop="creatorName"
                    label="创建人">
                </el-table-column>
                <el-table-column
                    prop="createTime"
                    label="创建时间">
                </el-table-column>
                <el-table-column
                    prop="updateName"
                    label="修改人">
                </el-table-column>
                <el-table-column
                    prop="updateTime"
                    label="修改时间">
                </el-table-column>
                <el-table-column
                    align="center"
                    width="120"
                    label="操作">
                    <template slot-scope="scope">
                        <el-link type="primary" @click="editOpen(scope.row)">修改</el-link>
                        <el-divider direction="vertical"></el-divider>
                        <el-link type="danger" @click="deleteSkillList(scope.row)">删除</el-link>
                    </template>
                </el-table-column>
            </el-table>
            <div class="block">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pages.pageNo"
                    :page-sizes="[8, 10, 20, 50]"
                    :page-size="pages.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pages.total">
                </el-pagination>
            </div>
        </div>
        <el-dialog
            :title="dialogTit"
            :visible.sync="dialogVisible"
            width="30%">
            <div style="padding: 10px 20px">
                <span>技能组名称：</span>
                <el-input
                    class="inputC"
                    placeholder="请输入技能组名称"
                    v-model="skillName"
                    size="mini"
                    clearable>
                </el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false" size="mini">取 消</el-button>
                <el-button type="primary"  size="mini" @click="editSkillList">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
  import Interface from '@/base/mixin/seat-skills';
  import {CP_PATH} from "@/config"
  export default {
    name: "skillConfig",
    mixins: [Interface],
    components:{

    },
    props:{

    },
    data(){
      return {
        selectValue:'',
        pages:{
          pageNo:1,
          pageSize:10,
          total:0,
        },
        tableData: [],
        dialogVisible: false,
        dialogTit: '新增',
        skillName: '',
        id:'',//单条数据id--修改用到
        loading:false,
        utime:'',
        ctime:'',
        creatorName:'',
      }
    },
    mounted() {
      this.getSkillList()
    },
    methods:{
      importJnCongigExcel(){
        window.location.href = CP_PATH+'/cpt-skillgroup-config/exportSkillGroup?creatorName='+this.creatorName+'&ctime='+this.ctime+'&utime='+this.utime+'&skillGroup='+this.selectValue
      },
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.getSkillList()
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.getSkillList()
        console.log(`当前页: ${val}`);
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex%2===0) {
          return 'success-row';
        }
        return '';
      },
      //技能配置列表
      getSkillList(){
        this.loading =true
        this.getSkillGroupData({
          "creatorName":this.creatorName,
          "ctime":this.ctime,
          "utime":this.utime,
          "skillGroup":this.selectValue,
          "current":this.pages.pageNo,
          "size":this.pages.pageSize,
        }).then(res=>{
          this.loading = false
          this.tableData = res.list
          this.pages.total = res.total
        }).catch(error=>{
          window.console.log(error)
        })
      },
      addSkillListOpen(){ //新增
        this.dialogTit = '新增'
        this.skillName = ''
        this.dialogVisible = true;
      },
      editOpen(row){//编辑
        this.dialogTit = '修改'
        this.skillName = row.skillGroup
        this.dialogVisible = true;
        this.id = row.id
      },
      editSkillList(){  //新增或者修改
        if (this.dialogTit==='新增'){
          this.addSkillGroup({
            "skillGroup":this.skillName,
          }).then(()=>{
            this.getSkillList()
            this.dialogVisible = false;
          }).catch(error=>{
            window.console.log(error)
          })
        }else {
          this.updateSkillGroup({
            "skillGroup":this.skillName,
            "id":this.id,
          }).then(()=>{
            this.getSkillList()
            this.dialogVisible = false;
          }).catch(error=>{
            window.console.log(error)
          })
        }
      },
      deleteSkillList(row){
        this.deleteSkillGroup({
          "id":row.id,
        }).then(()=>{
          this.getSkillList()
        }).catch(error=>{
          window.console.log(error)
        })
      }
    },
  }
</script>

<style scoped lang="scss">
    .skillConfig{
        ::v-deep .el-table{
            .success-row {
                background: rgb(244, 244, 244);
            }
            .cell{
                padding: 2px 4px;
            }
            th{
                background-color: rgb(24, 153, 250);
                .cell{
                    color: #ffffff;
                }
            }
        }
        .inputC{width: 160px;margin: 0px 6px}
        &>div{
            margin: 10px 0px;
        }
        .title{
            padding-left: 18px;
            border-left: 6px solid #1A84F4;
            font-weight: bold;
        }
        .nav{
            span{
                margin: 0px 2px 0px 20px;
            }
        }
    }
</style>